import DrawFlow from "@/components/DrawFlow/index.vue";
import AdminLayout from "@/layouts/Admin/index.vue";
import { defineComponent } from "vue";
import Loading from "vue3-loading-overlay";
import BlocksMenu from "@/components/BlocksMenu/index.vue";
import { mapActions, mapState } from "vuex";
import routes from "@/routes";
import EventEmitter from "@/util/EventEmitter";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";
import BotoModal from "@/components/BotoModal/index.vue";
import { v4 as uuidv4 } from "uuid";
import useVuelidate from "@vuelidate/core";
import { required, minLength, maxLength } from "@vuelidate/validators";
import Multiselect from "@vueform/multiselect";
import Toggle from "@vueform/toggle";

import "vue-toast-notification/dist/theme-sugar.css";
import "vue3-loading-overlay/dist/vue3-loading-overlay.css";
import "@vueform/multiselect/themes/default.css";

const untitledBoto = "Untitled Boto";

export default defineComponent({
  name: "Editor",
  setup() {
    return {
      v$: useVuelidate(),
      context: process.env.VUE_APP_CONTEXT,
    };
  },
  computed: {
    ...mapState("editor", ["template"]),
    ...mapState("auth", ["authToken"]),
  },
  data() {
    return {
      open: false,
      blocks: null,
      fullPage: true,
      bot: null,
      isLoading: true,
      activationShow: false,
      activationLoading: false,
      saveError: false,
      saveSuccess: false,
      saveLoading: false,
      isEditingName: false,
      isFlowValid: true,
      botNameValidation: true,
      usesTemplate: false,
      editorAreaHeight: 0,
      showingToast: false,
      dfKey: uuidv4(),
      toolsCollapsed: false,
      botFrequencyUnit: "MIN",
      triggerType: "SCH",
      scheduleOptions: [
        {
          label: this.$t("global.minutes"),
          value: "MIN",
        },
        {
          label: this.$t("global.hours"),
          value: "HR",
        },
        {
          label: this.$t("global.days"),
          value: "DAY",
        },
      ],
      calcBotFrequency: 10,
      botName: untitledBoto,
      preSaveBotName: "",
      botFrequency: 10,
      myIframe: null,
    };
  },
  components: {
    AdminLayout,
    Loading,
    DrawFlow,
    BlocksMenu,
    BotoModal,
    Multiselect,
    Toggle,
  },
  beforeUnmount() {
    this.bot = null;
  },
  validations() {
    return {
      botName: {
        required,
        minLength: minLength(5),
        maxLength: maxLength(80),
      },
    };
  },
  methods: {
    ...mapActions("editor", ["removeTemplateLocalStorage"]),
    updateScheduleOptions() {
      this.scheduleOptions = [
        {
          label: this.$t("global.minutes"),
          value: "MIN",
        },
        {
          label: this.$t("global.hours"),
          value: "HR",
        },
        {
          label: this.$t("global.days"),
          value: "DAY",
        },
      ];
    },
    validationStatusCheck() {
      this.botNameValidation = !this.v$.$invalid;
    },
    showToast(type: string, message: string) {
      this.$toast.open({
        message,
        type,
        duration: 5000,
        dismissible: true,
        position: "top-right",
      });
    },
    cleanSave() {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      setTimeout(() => {
        vm.saveLoading = false;
        vm.saveSuccess = false;
        vm.saveError = false;
      }, 3000);
    },
    clearBot() {
      this.bot = null;
      this.botName = untitledBoto;
      this.removeTemplateLocalStorage();
      // @ts-expect-error ignore
      this.$refs["drawflowEditor"].clearBot();
      const v = uuidv4();
      // TODO: cache issues
      window.location.href = `${this.context}editor?${v}`;
    },
    editBotName() {
      this.preSaveBotName = this.botName;
      this.isEditingName = true;
    },
    cancelPreSaveBotName() {
      this.botName = this.preSaveBotName;
      this.isEditingName = false;
    },
    savePreSaveBotName() {
      if (this.botNameValidation) this.isEditingName = false;
    },
    cancelActivateBot() {
      this.activationShow = false;
    },
    async activateBot() {
      try {
        this.activationLoading = true;
        await this.switchBoto("ON");
        routes.push({
          name: "home.botos",
          params: {
            botId: (this.bot as any).id,
            botName: (this.bot as any).name,
            botFrequency: (this.bot as any).frequency,
            botSchedule: (this.bot as any).schedule,
            botFrequencyUnit: (this.bot as any).frequency_unit,
          },
        });
      } catch (e) {
        console.error(e);
        this.showToast("error", HttpClient.getTextError(e));
        this.activationLoading = false;
      }
      this.activationShow = false;
    },
    toggleTools() {
      this.toolsCollapsed = !this.toolsCollapsed;
    },
    async saveBot() {
      try {
        if (this.isFlowValid) {
          this.saveLoading = true;
          this.saveSuccess = false;
          this.saveError = false;
          // @ts-expect-error ignore
          const jsonData = this.$refs["drawflowEditor"].export();
          const triggerType = this.triggerType;
          const body: any = {
            name: this.botName,
            json: jsonData.json,
            schedule: triggerType,
          };
          if (triggerType === "SCH") {
            body["frequency"] = this.calcBotFrequency;
            body["frequency_unit"] = this.botFrequencyUnit;
          }
          const method =
            this.bot && !this.usesTemplate
              ? HttpRequestMethod.PUT
              : HttpRequestMethod.POST;
          let url = `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bots/`;
          // @ts-expect-error ignore
          if (this.bot && !this.usesTemplate) url = `${url}${this.bot?.id}/`;
          const saveAction =
            !this.bot || (this.bot && this.usesTemplate)
              ? this.$t("global.created")
              : this.$t("global.updated");
          const saveMessage = this.$t("editor.saved_message", {
            action: saveAction,
          });
          const response = await HttpClient.request(
            url,
            this.authToken!,
            method,
            JSON.stringify(body)
          );
          this.showToast("success", saveMessage);
          this.removeTemplateLocalStorage();
          this.usesTemplate = false;
          this.bot = response;
          this.saveLoading = false;
          this.saveSuccess = true;
          this.cleanSave();
        }
      } catch (e) {
        this.showToast("error", HttpClient.getTextError(e));
        this.activationLoading = false;
        this.saveLoading = false;
        this.saveSuccess = false;
        this.saveError = true;
        console.error(e);
        this.cleanSave();
      }
    },
    async activateDeactivateBoto() {
      try {
        this.activationLoading = true;
        const status = (this.bot as any).status;
        await this.switchBoto(status);
        // TODO: need to do this, API not handling great the response
        (this.bot as any).status = status;
        const action =
          status === "ON"
            ? this.$t("editor.switched_on")
            : this.$t("editor.switched_off");
        this.showToast(
          "success",
          this.$t("editor.switched_message_ok", { action })
        );
        this.activationLoading = false;
      } catch (e) {
        this.showToast("error", this.$t("editor.switched_message_error"));
        this.activationLoading = false;
      }
    },
    async switchBoto(status: string) {
      try {
        const headers = new Headers();
        const formdata = new FormData();
        const url = `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${
          // @ts-expect-error ignore
          this.bot!.id
        }/`;
        // @ts-expect-error ignore
        formdata.append("name", this.bot!.name);
        formdata.append("status", status);
        // @ts-expect-error ignore
        formdata.append("frequency", this.bot!.frequency.toString());
        const requestOptions = {
          method: "PUT",
          headers,
          body: formdata,
        };
        headers.append("Authorization", `Token ${this.authToken}`);
        const response = await fetch(url, requestOptions);
        if (!response.ok) {
          throw new Error(
            this.$t("global.generic_error", { code: response.status })
          );
        }
      } catch (e) {
        this.showToast("error", HttpClient.getTextError(e));
        this.activationLoading = false;
        console.error(e);
      }
    },
    async getBlocks() {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/my/blocks/`,
        this.authToken!,
        HttpRequestMethod.GET
      );
    },
    async getBot(id: string) {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${id}/`,
        this.authToken!,
        HttpRequestMethod.GET
      );
    },
    async getTemplate(templateId: string) {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot_template/${templateId}/`,
        null,
        HttpRequestMethod.GET
      );
    },
    resetEventListeners() {
      EventEmitter.emitter.off("editor-block-update-block-updated");
      EventEmitter.emitter.off("editor-valid-status");
      EventEmitter.emitter.off("emit-toast");
    },
    whenChange() {
      // Workaround for backend bug
      if (this.botFrequencyUnit === "0") this.botFrequencyUnit = "MIN";
    },
    calculateRealFrequency() {
      switch (this.botFrequencyUnit) {
        case "MIN":
          this.calcBotFrequency = this.botFrequency;
          break;
        case "HR":
          this.calcBotFrequency = this.botFrequency * 60;
          break;
        case "DAY":
          this.calcBotFrequency = this.botFrequency * 24 * 60;
          break;
      }
    },
    onFrequencyChange(val: number) {
      this.botFrequency = val;
      this.calculateRealFrequency();
    },
    getDisplayFrequency(frequency: number, unit: string) {
      let result = frequency;
      switch (unit) {
        case "HR":
          result = frequency / 60;
          break;
        case "DAY":
          result = frequency / 24 / 60;
          break;
      }
      return result;
    },
    frequencyUnitChange(val: string) {
      this.botFrequencyUnit = val;
      this.calculateRealFrequency();
    },
  },
  created() {
    if (!navigator.onLine) {
      routes.push({
        name: "home.offline",
        params: { route: this.$route.fullPath },
      });
    }
    // CTRL+S
    document.addEventListener("keydown", (e) => {
      if (e.ctrlKey && e.key === "s") {
        e.preventDefault();
        this.saveBot();
      }
    });
    EventEmitter.emitter.on("locale-changed", (locale: string) => {
      this.$i18n.locale = locale;
      this.updateScheduleOptions();
    });
  },
  async mounted() {
    try {
      this.resetEventListeners();
      const template = this.template;
      this.blocks = await this.getBlocks(); // TODO: caching?
      const botId = this.$route.params.id;
      const templateId = this.$route.query.templateId?.toString();
      const type = this.$route.query.type;
      // FLOW: Editing Boto
      if (botId) {
        this.removeTemplateLocalStorage();
        const bot = await this.getBot(botId.toString());
        this.bot = bot;
        this.usesTemplate = false;
        this.botName = bot.name;
        this.botFrequencyUnit = bot.frequency_unit || "MIN";
        this.calcBotFrequency = bot.frequency;
        this.triggerType = bot.schedule;
        this.botFrequency = this.getDisplayFrequency(
          bot.frequency,
          this.botFrequencyUnit
        );
      }
      // FLOW: Creating Boto from prexistent boto or recipe
      // template comes from vuex
      else if (templateId) {
        let bot;
        // prexistent boto
        if (type === "copy") {
          bot = await this.getBot(templateId);
          bot.name = template?.name || bot.name;
          bot.id = null;
          bot.copy = true;
        }
        // recipe
        // else if (type === "recipe") {
        else {
          // no need to request the template if it was stored in the localStorage
          // use case: home page recipe editor
          bot =
            template?.clone_of.toString() === templateId
              ? template
              : await this.getTemplate(templateId);
          console.log();
          delete bot.id;
        }
        this.botName = bot.name || untitledBoto;
        this.bot = bot;
        this.usesTemplate = true;
        // copied bot
        if (this.bot && (this.bot as any).copy) {
          this.showToast("success", this.$t("editor.copied_message"));
          delete (this.bot as any).copy;
        }
      }

      this.isFlowValid = !this.v$.$invalid;
      this.isLoading = false;
      EventEmitter.emitter.on("editor-valid-status", (status: boolean) => {
        this.isFlowValid = status;
      });
      EventEmitter.emitter.on("editor-trigger-type", (triggerType: string) => {
        this.triggerType = triggerType;
      });
      EventEmitter.emitter.on("emit-toast", (type: string, text: string) => {
        this.showToast(type, text);
      });
      EventEmitter.emitter.on(
        "editor-block-update-block-updated",
        (id: string, status: boolean, block: any) => {
          if (status) {
            this.showToast(
              "success",
              this.$t("editor.block_upgrade_message", { name: block.name })
            );
          } else {
            this.showToast("error", this.$t("editor.block_upgrade_error"));
          }
        }
      );
      setTimeout(() => {
        EventEmitter.emitter.emit("sidebar-toggle-sidebar", true);
      }, 100);
    } catch (e) {
      console.error(e);
      this.showToast("error", HttpClient.getTextError(e));
      this.isLoading = false;
    }
  },
});
